<template>
  <div>
    <div class="pagebar-title">
      {{ title }}
      <el-button @click="push()" style="float: right">提交</el-button>
    </div>

    <el-row :gutter="20">
      <el-col :span="12">
        <div class="module">
          <div class="mtitle">参数名</div>
          <div class="li">
            <el-input v-model="form.name" placeholder="参数名称"></el-input>
          </div>
        </div>

        <el-row :gutter="20">
          <el-col :span="12">
            <div class="module">
              <div class="mtitle">总线地址</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_busadd"
                  placeholder="总线地址"
                ></el-input>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="module">
              <div class="mtitle">功能号</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_featurescode"
                  placeholder="功能号"
                ></el-input>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="12">
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="module">
              <div class="mtitle">寄存器起始地址</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_registeraddr_start"
                  placeholder="寄存器起始地址"
                ></el-input>
              </div>
            </div>

            <div class="module">
              <div class="mtitle">结束位</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_registeraddr_end"
                  placeholder="结束位"
                ></el-input>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="module">
              <div class="mtitle">读取寄存器个数</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_read_start"
                  placeholder="读取寄存器个数"
                ></el-input>
              </div>
            </div>

            <div class="module">
              <div class="mtitle">结束位</div>
              <div class="li">
                <el-input
                  v-model="form.modbus_read_end"
                  placeholder="结束位"
                ></el-input>
              </div>
            </div>
          </el-col>
        </el-row>

        <!-- <div class="module">
      <div class="mtitle">参数模板ID</div>
      <div class="li">
          <el-input v-model="form.paramtemplateid" placeholder="参数模板ID"></el-input>
      </div>
    </div> -->
      </el-col>
    </el-row>

    <div class="module">
      <div class="mtitle">参数模板</div>
      <div class="li">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input
              v-model="form.paramtemplateid"
              placeholder="参数模板"
            ></el-input>
          </el-col>
          <el-col :span="18">
            {{ sel_paramtemplate.name }} - {{ sel_paramtemplate.decimal }} -
            {{ sel_paramtemplate.ext }} - {{ sel_paramtemplate.fieldname }}
          </el-col>
        </el-row>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="12">
        <div class="module">
          <div class="mtitle">选择参数模板</div>
          <div class="li">
            <el-table
              :data="getList_paramtemplateData_ram.tableData"
              style="width: 100%"
              @row-click="openDetails"
            >
              <el-table-column
                prop="id"
                label="模板ID"
                width="100"
                align="center"
              >
              </el-table-column>

              <el-table-column prop="name" label="模板名"></el-table-column>
            </el-table>
          </div>
        </div>
        <div class="module">
          <Pagination
            @pagination="getList_paramtemplateData()"
            :page.sync="getList_paramtemplateData_ram.querypage"
            :pageCount.sync="getList_paramtemplateData_ram.totalPage"
            :total.sync="getList_paramtemplateData_ram.total"
            v-if="getList_paramtemplateData_ram.total > 0"
          />
        </div>
      </el-col>
      <el-col :span="12" v-if="getList_paramtemplateItemData_ram.tableData">
        <div class="module">
          <div class="mtitle">选择参数</div>
          <div class="li">
            <el-table
              :data="getList_paramtemplateItemData_ram.tableData"
              style="width: 100%"
              @row-click="selitem"
            >
              <el-table-column prop="id" label="ID" width="50" align="center">
              </el-table-column>

              <el-table-column
                prop="decimal"
                label="小数位"
                width="100"
                align="center"
              >
              </el-table-column>

              <el-table-column
                prop="ext"
                label="后缀名"
                width="150"
                align="center"
              >
              </el-table-column>

              <el-table-column
                prop="fieldname"
                label="参数字段名"
                align="center"
              >
              </el-table-column>

              <el-table-column prop="name" label="参数名"> </el-table-column>
            </el-table>
          </div>
        </div>
      </el-col>
    </el-row>

    <!-- <div class="module">
      <el-button @click="Saveusername()">保存用户名</el-button>
    </div> -->
  </div>
</template>

<script>
import { Add } from "@/api/equipment";
import { Categorylist, Itemlist } from "@/api/paramtemplate";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "ParamsEditOrAdd",
  data() {
    return {
      title: null,
      form: {
        id: null,
        gateway_id: null,
        bodyid: null,
        paramsid: null,
        name: null,
        modbus_busadd: null,
        modbus_featurescode: null,
        modbus_registeraddr_start: null,
        modbus_registeraddr_end: null,
        modbus_read_start: null,
        modbus_read_end: null,
        paramtemplateid: null,
      },
      paramtemplate: {},
      sel_paramtemplate: "",
      // 参数模板列表
      getList_paramtemplateData_ram: {
        currentPage: 1, // 当前页码
        total: 0, // 总条目
        totalPage: 0, // 总页数
        tableData: [],
      },
      getList_paramtemplateItemData_ram: {
        tableData: [],
      },
    };
  },
  methods: {
    push() {
      this.$http(Add(this.form), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          (this.updateTable = {
            name: "",
            gbcode: "",
          }),
            this.getList();
          this.dialogVisible = false;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    openDetails(row) {
      this.getList_paramtemplateItemData(row.id);
      //具体操作
    },
    selitem(row) {
      console.log(row);
      this.form.paramtemplateid = row.id;
      this.sel_paramtemplate = row;
    },
    getList_paramtemplateData() {
      this.$http(
        Categorylist({
          querypage: this.currentPage,
        }),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.getList_paramtemplateData_ram.total = res.data.total;
            this.getList_paramtemplateData_ram.currentPage =
              res.data.currentPage;
            this.getList_paramtemplateData_ram.totalPage = res.data.totalPages;
            this.getList_paramtemplateData_ram.tableData = res.data.result;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
    getList_paramtemplateItemData(categoryid) {
      this.$http(
        Itemlist({
          categoryid: categoryid,
        }),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.getList_paramtemplateItemData_ram.tableData = res.data;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
  },
  components: {
    Pagination,
  },
  created() {
    if (this.$route.query.paramsid) {
      this.form.paramsid = this.$route.query.paramsid;
      if (this.$route.params) {
        let cache = this.$route.params;
        let form = this.form;

        console.log(cache);

        this.title =
          "编辑当前参数 所属网关ID: " +
          cache.gateway_id +
          " 从机ID: " +
          cache.bodyid;

        form.paramsid = this.$route.query.paramsid;
        form.id = cache.id;
        form.bodyid = cache.bodyid;
        form.gateway_id = cache.gateway_id;
        form.name = cache.name;
        form.modbus_busadd = cache.modbus_busadd;
        form.modbus_featurescode = cache.modbus_featurescode;
        form.modbus_registeraddr_start = cache.modbus_registeraddr_start;
        form.modbus_registeraddr_end = cache.modbus_registeraddr_end;
        form.modbus_read_start = cache.modbus_read_start;
        form.modbus_read_end = cache.modbus_read_end;
        form.paramtemplateid = cache.paramtemplateid;
        this.sel_paramtemplate = cache.paramtemplate;
      }
    } else {
      this.title =
        "新增从机参数 所属网关ID: " +
        this.$route.query.gateway_id +
        " 从机ID: " +
        this.$route.query.bodyid;
      this.form.gateway_id = this.$route.query.gateway_id;
      this.form.bodyid = this.$route.query.bodyid;
      console.log("新增模式");
    }
    this.getList_paramtemplateData();
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>
